import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/app',
      name: 'app',
      component: () => import('./views/app/App.vue'),
      children: [
        {
          path: 'dashboard',
          name: 'dashboard',
          component: () => import('./views/app/dashboard/index.vue'),
        },
        {
          path: 'prediksi',
          name: 'prediksi',
          component: () => import('./views/app/prediksi/index.vue'),
        },
        {
          path: 'data',
          name: 'data',
          component: () => import('./views/app/data/list.vue'),
        },
        {
          path: 'add-data',
          name: 'add-data',
          component: () => import('./views/app/data/add.vue'),
        },
        {
          path: 'edit-data/:id',
          name: 'edit-data',
          props: true,
          component: () => import('./views/app/data/update.vue'),
        },
        {
          path: 'rules',
          name: 'rules',
          component: () => import('./views/app/rules/list.vue'),
        },
        {
          path: 'add-rule',
          name: 'add-rule',
          component: () => import('./views/app/rules/add.vue'),
        },
        {
          path: 'edit-rule/:id',
          name: 'edit-rule',
          props: true,
          component: () => import('./views/app/rules/update.vue'),
        },
      ]
    },
    {
      path: '/about',
      name: 'about',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () => import(/* webpackChunkName: "about" */ './views/About.vue')
    }
  ]
})
